@import 'main';
// End Of Import

.navover {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba($color-sec, 0.8);
  z-index: 9;
  transition: .5s;
  opacity: 0;
  visibility: hidden;

  &.open {
      opacity: 1;
      visibility: visible;
  }
}

.sideNav {
  position: fixed;
  top: 0;
  right: -261px;
  width: 260px;
  height: 100%;
  background-color: #FFF;
  z-index: 99;
  overflow-y: auto;
  transition: .5s;

  &.open{
      right: 0;
  }

  .options {
      padding: 20px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .lang a {
          display: flex;
          align-items: center;
          justify-content: center;
          color: #3c3c3c;
      }

      .close1 {
          display: flex;
          font-size: 20px;
          color: #3c3c3c;
          cursor: pointer;
      }
  }

  & > ul {
      display: flex;
      align-items: center;
      flex-direction: column;
      width: 100%;
      // overflow: auto;
      padding: 0px 25px 0;

      & > li{
          position: relative;
          padding: 7px 15px;
          width: 100%;

          &:not(:last-child) {
              border-bottom: 1px solid #dedede;
          }

          a {
              width: 100%;
              display: block;
              font-size: 16px;
              color: #3c3c3c;
              transition: .5s;
          }

          i{
              position: absolute;
              top: 13px;
              left: 13px;
              transform: rotate(90deg);
              font-size: 9px;
              color: currentColor;
              transition: .5s;

              &.open{
                  transform: rotate(0deg);
              }
          }

          > span{
            margin-bottom: 10px;
            display: block;
            width: 100%;
            position: relative;
            font-size: 16px;
            color: #3c3c3c;

            &:after {
              content: "\002B";
              font-size: 30px;
              position: absolute;
              left: 5px;
              top: 50%;
              -webkit-transform: translateY(-50%);
              -ms-transform: translateY(-50%);
              transform: translateY(-50%);
              font-size: 18px;
              -webkit-transition: 0.3s;
              -o-transition: 0.3s;
              transition: 0.3s;
              color: #3c3c3c;
            }
          }

          ul{
            display: none;

            li{
              &:not(:last-child){
                border-bottom: 1px solid #dedede;
                margin-bottom: 5px
              }
            }
          }

          &.open{
            span{
              &:after{
                content: "\2212"
              }
            }
          }
      }
  }
}

