// Slider Styles

.owl-dots {
  position: absolute;
  bottom: 40px;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;

  @include Xs {
    bottom: -50px;
  }

  button {
    &:not(:last-child) {
