// Add Order
.orderAdd{
  margin-top: 40px;

  h1{
    font-size: 24px;
    color: #185eb2;
    text-align: center;
    margin-bottom: 50px;
  }

  form{

    input,label,textarea{
      display: block;
      width: 100%;
      resize: none;
    }

    label{
      color: #3c3c3c;
      font-size: 17px;
      text-align: center;
      margin-bottom: 10px;


      &:first-of-type{

        &:after{
          content: "*";
          color: red;
        }
      }

      &:not(:first-of-type){
        margin-top: 20px;
      }

      &.up{
        margin-bottom: 0;

        > span{
          margin-bottom: 10px;
          display: block;
        }

        .upload{
          position: relative;
          height: 55px;
          border-radius: 27px;
          background-color: #FBFBFB;
          border: 1px solid #E5E7E7;
          cursor: pointer;

          input{
            opacity: 0;
            visibility: hidden;
          }

          span{
            width: 120px;
            height: 43px;
            border-radius: 50px;
            background-color: #E5E5E5;
            color: #a2a1a1;
            font-size: 16px;
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            top: 50%;
            left: 7px;
            transform: translateY(-51%);
            transition: .5s;

            &:hover{
              background-color: $color-sec;
              color: #FFF;
            }
          }
        }
      }
    }

    input{
      padding: 0 30px;
      height: 55px;
      border-radius: 27px;
      background-color: #FBFBFB;
      border: 1px solid #E5E7E7;
      transition: .5s;

      &:focus{
        outline: none;
        border: 1px solid $color-prim;
      }
    }

    textarea{
      padding: 10px 30px;
      border-radius: 27px;
      background-color: #FBFBFB;
      border: 1px solid #E5E7E7;
      min-height: 150px;
      transition: .5s;

      &:focus{
        outline: none;
        border: 1px solid $color-prim;
      }
    }

    > .sEnd{
      width: 100%;
      height: 45px;
      border-radius: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #FFF;
      font-size: 16px;
      font-family: "frtBold";
      background-color: $color-prim;
      border: 0;
      margin-top: 20px;
      transition: .5s;

      &:hover{
        background-color: $color-sec;
      }
    }
  }
}

