.PoPUP{
  background-color: rgba(0, 0, 0, .3);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: .5s;

  &.active{
    opacity: 1;
    visibility: visible;
    backdrop-filter: blur(2px);
  }
}

.PoPUPin{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(.9);
  width: 475px;
  height: 215px;
  background-color: #FFF;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  opacity: 0;
  visibility: hidden;
  z-index: 99;
  transition: .5s;

  &.active{
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
    visibility: visible;
  }

  @include mob600{
    width: calc(100% - 30px);
    margin: auto;
  }

  p{
    font-size: 20px;
    color: #3c3c3c;
    margin-bottom: 35px;
  }

  button{
    width: 140px;
    height: 40px;
    border-radius: 50px;
    border: 0;
    font-size: 16px;
    transition: .5s;
    
    &:first-child{
      color: #FFF;
      background-color: #67B6D5;

      &:hover{
        background-color: $color-prim;
      }
    }
    
    &:last-child{
      color: #7e7e7e;
      background-color: #DCDCDC;
      
      &:hover{
        background-color: $color-prim;
        color: #FFF;
      }
    }
  }
}